.text-sphere {
    /* position: relative; */
    /* top : 0; */
    background-color: black;
    border-radius: 50%;
    border: 2px solid white;
    width: max-content;
    height: max-content;
    position: absolute;
    top: 2.5%;
    right: 8.2%;
    /* display: none; */
  }
  
  .tagcloud{
    display: inline-block;
    top: 0;
    left: 0;
    font-weight: 600;
    letter-spacing: 0.0635em;
    font-size: 1.3em;
  }

  @media screen and (max-width : 767px) {

    .text-sphere {
      position: absolute;
    top: 5%;
    right: 0%;
    display : none;
    }

    .tagcloud {
      font-size: 0.9em;
    }
    
  }


  @media (min-width :  768px) and (max-width : 1024px) {

    .text-sphere {
      position: absolute;
    top: 5%;
    left : 20%;
    display: none;
    }

    .tagcloud {
      font-size: 1.0em;
    }
    
  }

  
  .tagcloud--item{
    color: rgb(31,229,222);
    text-transform: uppercase;
  }
  
  .tagcloud--item:hover{
    color: white;
  }
  